/* Обновление стилей для overlay */
.modal-task-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  z-index: 1000;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.modal-task-overlay.open {
  opacity: 1;
}

.modal-task {
  background-color: #091533;
  padding: 20px;
  border-radius: 30px 30px 0 0;
  width: 100%;
  max-width: 600px;
  text-align: center;
  position: relative;
  box-shadow: 0 -4px 8px rgba(0, 0, 0, 0.2);
  z-index: 1100;
  transform: translateY(100%);
  opacity: 0;
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.modal-task.open {
  transform: translateY(0);
  opacity: 1;
}

/* Стиль для кнопки закрытия с круглым синим фоном */
.close-button {
  position: absolute;
  top: 15px;
  right: 15px;
  background-color: #182c5f; 
  border: none;
  color: white;
  cursor: pointer;
  border-radius: 50%; 
  width: 30px; 
  height: 30px; 
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  padding-top: 5px;
}

/* Стиль для SVG внутри кнопки */
.close-button svg {
  width: 20px; 
  height: 20px; 
  stroke: white;
}

/* Логотип и прочие стили */
.modal-logo-container {
  margin-bottom: 20px;
}

.modal-logo {
  width: 100px;
  height: 100px;
}

.modal-task h2 {
  font-size: 24px;
  color: white;
  margin-bottom: 10px;
}

.modal-task p {
  font-size: 16px;
  color: white;
  margin-bottom: 20px;
}

.reward-info {
  font-size: 18px;
  color: #ADC3FF;
  margin-bottom: 20px;
}

.subscribe-button, .check-task-button {
  width: 100%;
  padding: 15px;
  margin-bottom: 10px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
}

.subscribe-button {
  background-color: #284286;
  color: white;
}

.subscribe-button:hover {
  background-color: #1a3069;
}

.check-task-button {
  background-color: #009ACD;
  color: white;
}

.check-task-button:hover {
  background-color: #007BB5;
}

.check-task-button.disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.error-message {
  color: red;
  margin-top: 10px;
}

.info-message {
  color: #007BFF;
  margin-top: 10px;
}